<template>
  <div class="main">
    <div class="header">
      <div class="head">
        <router-link to="/index" class="back"></router-link
        >{{ $t('lang.login') }}
      </div>
    </div>
    <div class="top_bg">
      <h4>{{ $t('lang.welcome') }}</h4>
      <p>{{ $t('lang.please_login') }}</p>
      <!-- <p>{{ $t('lang.please_login') }}</p> -->
    </div>
    <div class="newbg">
      <div class="choose_btn_wrap">
        <div class="choose_btn">
          <router-link to="/login" class="l_btn cur"
            >{{ $t('lang.login') }}
          </router-link>
          <router-link to="/register" class="l_btn1"
            >{{ $t('lang.register') }}
          </router-link>
        </div>
      </div>
      <form class="form login" @submit.prevent="handleSubmit">
        <div class="item">
          <div class="content">
            <p>{{ $t('lang.mobile') }}</p>
            <input
              v-model.trim="data.username"
              class="inp"
              type="text"
              :placeholder="$t('lang.comments_phone_placeholder')"
            />
            <!-- <span class="left_icon phone_icon"></span>
            <input
              v-model.trim="data.username"
              class="inp"
              type="text"
              :placeholder="$t('lang.input_email')"
            />
            <span class="right_icon clear_icon" @click="clear()"></span> -->
          </div>
        </div>
        <div class="item item_pwd">
          <div class="content">
            <p>{{ $t('lang.login_pw') }}</p>
            <input
              v-model.trim="data.password"
              autocomplete="off"
              class="inp"
              :type="psd"
              :placeholder="$t('lang.input_pw')"
            />
            <!-- <span class="left_icon pwd_icon"></span>
            <input
              v-model.trim="data.password"
              autocomplete="off"
              class="inp"
              :type="psd"
              :placeholder="$t('lang.input_pw')"
            />
            <span
              class="right_icon show_icon"
              :class="psd == 'password' ? '' : 'show'"
              @click="show_psd"
            ></span> -->
          </div>
        </div>
        <div class="item item_forget">
          <router-link to="/forgetpwd" class="forgetpwd"
            >{{ $t('lang.fotget_pw') }} ？</router-link
          >
        </div>

        <div class="item login_box">
          <button
            type="submit"
            class="btn basic_btn_v2"
            :class="
              data.username != '' && data.password != '' ? '' : 'no_touch'
            "
          >
            {{ $t('lang.login_now') }}
          </button>
        </div>
        <label class="contract">
          <van-checkbox
            v-model="checked"
            icon-size="15px"
            color="#00B389"
            sytle="color:#00B389;"
            >{{ $t('lang.has_agree') }}
          </van-checkbox>
          <!--                <router-link to="/regContact">用户协议</router-link>-->
          <router-link
            style="margin-left: 5px"
            v-if="config.user_contract_name"
            :to="config.user_contract_link"
            >《{{ config.user_contract_name }}》</router-link
          >
          <router-link
            style=""
            v-if="config.user_contract_ys_name"
            :to="config.user_contract_ys_link"
            >《{{ config.user_contract_ys_name }}》</router-link
          >
        </label>
      </form>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import qs from 'qs';
import { CountDown, Checkbox } from 'vant';
import Fetch from '../../utils/fetch';

Vue.use(CountDown).use(Checkbox);
export default {
  name: 'index',
  components: {},
  data() {
    return {
      data: {},
      bg: '',
      checked: true,
      config: {},
      psd: 'password',
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    this.start();
  },
  methods: {
    start() {
      Fetch('/api/api/webconfig', { type: 'bg' }).then(res => {
        this.bg = 'url("' + res.data.login + '")';
      });
      Fetch('/api/api/webconfig', { type: 'web' }).then(res => {
        this.config = res.data;
      });
    },
    clear() {
      this.data.username = '';
    },
    show_psd() {
      console.log(this.psd);
      if (this.psd == 'password') {
        this.psd = 'text';
      } else {
        this.psd = 'password';
      }
    },

    handleSubmit() {
      if (!this.data.username) {
        this.$notify({
          message: this.$t('lang.input_mobile'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.data.password) {
        this.$notify({
          message: this.$t('lang.input_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.checked) {
        this.$notify({
          message: this.$t('lang.need_agree'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      Fetch('/api/api/login', {
        username: this.data.username,
        password: this.data.password,
      }).then(res => {
        this.$parent.setFooterType(res.data.footer);
        if (res.data.footer === 'n1') {
          this.$router.replace('/user');
        } else {
          this.$router.replace({
            path: this.$router.history.current.query.redirect || '/',
          });
        }

        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.login_success'),
        });
      });
    },
  },
};
</script>

<style lang="less" scoped>
@import './css/login.less';
</style>
